<script>
import lang from "vee-validate/dist/locale/zh_CN";

import api from "../lib/api";
// import validator   from '../directive/validator';
import AdminNav from "../components/AdminNav.vue";
import Nav from "../components/Nav.vue";
import Pagination from "../components/Pagination.vue";
import AdminFooter from "../components/AdminFooter";
import Dropdown from "../components/Dropdown";

export default {
  // directives : { validator },
  components: { AdminNav, Nav, Pagination, AdminFooter, Dropdown },

  mounted() {
    if (!this.model) {
      throw new Error("datamodel");
    }

    // 
    this.read();
  },

  data() {
    return {
      // 
      model: null, // 'user', 'vehicle'

      // 
      keyword: "", // 
      searchable: [], // 

      // 
      show_form: false, // 

      // 
      current: {}, // 
      list: [], // 

      // 
      current_page: 1, // 
      last_page: 0, // 
      total: 0, // 
      limit: 5 // 
    };
  },

  methods: {
    set_validator_attr(attr) {
      this.$validator.localize("zh_CN", {
        messages: lang.messages,
        attributes: attr
      });

      // start with english locale.
      this.$validator.localize("zh_CN");
    },

    /**
     * 
     * @param {number} 
     * */
    on_page_change(page) {
      this.read(page);
    },

    /**
     * 
     * create or update
     *
     * id
     */
    cou(e) {
      e.preventDefault();

      let action = this.current.id ? "update" : "create";

      api(`${this.model}/${action}`, this.current).then(r => {
        this.current = {};
        this.read();
        this.show_form = false;
      });
    },

    /**
     * this.list
     * @param {number} 
     * */
    read(page = 1) {
      if (page == this.current_page && page != 1) return;
 
      api(`${this.model}/read`, {
        limit: this.limit,
        page: page,
        with: this.with
      }).then(r => {
        this.total = r.total;
        this.list = r.data;
        this.last_page = r.last_page;
        this.current_page = r.current_page;
      });
    },

    /**
     * id
     * @param {number} id
     * */
    remove(id) {
      if (!confirm("")) return;

      api(`${this.model}/delete`, { id }).then(r => {
        this.read();
      });
    },

    /**
     * 
     * 
     * @param row
     */
    set_current(row) {
      this.current = row;
      this.show_form = true;
      if (this.after_set_current) {
        this.after_set_current();
      }
    },

    /**
     * 
     * @param {Event} e
     */
    search(e) {
      e.preventDefault();

      let param = {
        or: {}
      };

      this.searchable.forEach(prop => {
        param.or[prop] = this.keyword;
      });

      api(`${this.model}/search`, param).then(r => {
        this.list = r.data;
      });
    },

    is_update() {
      return !!this.current.id;
    }
  }
};
</script>