<template>
  <div>
    <Nav/>
    <div>
      <div class='container'>
        <div class='header'>
          <form class="main-form" @submit="submit($event)" autocomplete="off">
            <h1 class='center'></h1>

            <div class="row center tab-title">
              <div @click="signup_by = 'phone'" :class="'col-lg-6 ' + (signup_by == 'phone' ? 'active': '')"></div>
              <div @click="signup_by = 'mail'" :class="'col-lg-6 ' + (signup_by == 'mail' ? 'active': '')"></div>
            </div>

            <div :key=1 v-if="signup_by=='phone'" class='input-control'>
              <label for='phone'></label>
              <input  v-model='current.phone' 
                     error-el="#phonp-error" 
                      
                     id="phone" 
                     type="text" 
                     placeholder="">
              <div class="error-list">
                <div id="phone-error"></div>
              </div>
            </div>

            <div :key=2 v-if="signup_by=='mail'" class='input-control'>
              <label for='mail'></label>
              <input  v-model='current.mail'
                     error-el="#mail-error"
                     id="mail" type="text" 
                     placeholder="">
              <div class="error-list">
                <div id="mail-error"></div>
              </div>
            </div>
            

            <!-- <div class="input-control">
              <label for="vcode"></label>
              <div class='btn-group'>
                <input type="text" style='width:70%;' id='vcode'  error-el="#vcode-error" v-model='current.vcode' placeholder="">
                <button @click='send_code' style='width:30%;' :disabled='captcha.countdown !=0' type='button'>
                  <span v-if='captcha.countdown'>{{captcha.countdown}}</span>
                  <span class='small' v-else></span>
                </button>
              </div>
              <div class='error-list'>
                <div v-if='invalid_code' class='error'></div>
                <div id='vcode-error'></div>
              </div>
            </div> -->
            <div>

               <label  class='code'></label>
            <div class="input-group">

              <input type="text" class="form-control"  autofocus   error-el="#vcode-error" v-model='current.vcode' >
              <span class="input-group-addon" @click='send_code' :disabled='captcha.countdown !=0'>
                 <span v-if='captcha.countdown'>{{captcha.countdown}}</span>
                 <span v-else></span>
                </span>
            </div>
             <div class='error-list'>
                <div v-if='invalid_code' class='error'></div>
                <div id='vcode-error'></div>
              </div>
            </div>

           
            <div class="input-control">
              <label for="password"></label>
              <input 
              
               error-el="#password-error"
                id="password"
                v-model='current.password' 
                type="password">
              <div class="error-list">
                <div id="password-error"></div>
              </div>
            </div>
            
            <div class="input-control">
              <label for="password2"></label>
              <input  v-model='current.password2' id="password2" type="password">
            </div>
            <div class="input-control last">
              <button class="btn-primary" type="submit"></button>
            </div>
            <div class="input-control small muted">
              
              <router-link to='/login'></router-link>
            </div>
          </form>
        </div>
      </div>
        
    </div>

  </div>
</template>
<script>
//import '../css/sigle-form.css'
import Nav from "../components/Nav.vue";
//import AdminFooter from "../components/AdminFooter.vue";
import api from "../lib/api";
import session from "../lib/session.js";

//import validator from "../directive/validator";

export default {
  
  components: { Nav},
  data() {
    return {
      captcha: {
        timer: null,
        countdown: 0
      },
      signup_by: "phone",
      current: {},
      code: "",
      invalid_code: false
    };
  },
  methods: {
    submit(e) {
      // e.preventDfault();
      e.preventDefault();

      //html
      this.invalid_code = this.current.vcode !== this.code;

      if (this.invalid_code) return;

      if (this.signup_by == "mail")
        //signup_bymail 
        delete this.current.phone;
      else {
        delete this.current.mail; //
      }
      //
      !this.current.username &&
        (this.current.username = this.current[this.signup_by]);

      api("user/create", this.current) //create
        .then(r => {
          session.login(r.data);
          alert("!");
          this.$router.push("/"); //
        });
    },

    send_code() {
      //
      console.log(1);

      if (this.captcha.countdown)
        //
        return;

      let action, by_mail;

      this.captcha.countdown = 60; //60
      action = "sms";

      if ((by_mail = this.signup_by == "mail")) action = "mail";

      if (
        (by_mail && !this.current.mail) || //
        (!by_mail && !this.current.phone)
      )
        return;

      this.captcha.timer = setInterval(() => {
        //
        if (this.captcha.countdown == 0) {
          //0
          clearInterval(this.captcha.timer);

          return;
        }
        this.$set(this.captcha, "countdown", this.captcha.countdown - 1); //1
      }, 1000);

      api(`captcha/${action}`, {
        phone: this.current.phone,
        mail: this.current.mail
      })
        //phonemail
        .then(r => {
          this.code = atob(r.data.result); //atob
        });
    }
  }
};
</script>
<style scoped>
/* .header{
  position: absolute;
  right:500px;
  margin-top:50px;
   
} */
.btn-primary

{
  background:rgb(231, 160, 160);
  border-color:rgb(231, 160, 160);
  
}
.btn-primary:hover{
      background: rgb(248, 130, 130);
      border-color: rgb(248, 130, 130);
  }

button[type='button']{
  
  margin-left: 0;
}
.input-contail [type="submit"] {
  padding: 10px;
  margin: 0;
}
.main-form {
  width: 370px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

.gologin {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.4);
  margin-top: 20px;
  margin-bottom: 30px;
}

.input-control.last {
  padding-top: 10px;
  padding-bottom: 10px;
}
.input-control.small.muted {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.4);
}
.small {
  font-size:3px;
}

.tab-title {
  color: #777;
  margin-bottom: 10px;
}

.tab-title > * {
  cursor: pointer;
  padding: 5px;
}
.tab-title > *.active {
  color: rgb(243, 154, 154);
  border-bottom: 2px solid rgb(243, 154, 154);
}
.code{
  margin-top: 15px;
}
.input-group-addon,
.form-control {
  border-radius:0px;
}
.input-group{
  border:2px solid rosybrown;
  

}
.input-group-addon{
  background:rgb(243, 154, 154);
  border:rgb(243, 154, 154);
  color:#fff;
}

</style>

